.all__columns {
  width: 6000px;
  display: flex;
  flex-direction: row;
  margin-left: 1000px;
  animation: columns 20s linear forwards;
}

.columns {
  @extend %columns;
  margin-right: 200px;
  &:first-child {
    margin-top: -500px;
  }
  &:nth-child(2) {
    margin-top: -300px;
  }
  &:nth-child(3) {
    margin-top: -450px;
  }
  &:nth-child(4) {
    margin-top: -300px;
  }
  &:nth-child(5) {
    margin-top: -400px;
  }
  &:nth-child(6) {
    margin-top: -250px;
  }
  &:nth-child(7) {
    margin-top: -450px;
  }
  &:nth-child(8) {
    margin-top: -300px;
  }
  &:nth-child(9) {
    margin-top: -250px;
  }
  &:nth-child(10) {
    margin-top: -450px;
  }
  &:nth-child(11) {
    margin-top: -400px;
  }
  &:nth-child(12) {
    margin-top: -350px;
  }
  &:nth-child(13) {
    margin-top: -250px;
  }
  &__top {
    @extend %column;
    .light__shadow {
      @extend %light__shadow;
    }
    &__head {
      @extend %column_head;
      bottom: -50px;
    }
  }
  &__bottom {
    @extend %column;
    .light__shadow {
      @extend %light__shadow;
    }
    &__head {
      @extend %column_head;
      top: -50px;
    }
  }
}
