.bg {
  width: 6400px;
  height: 100px;
  position: absolute;
  bottom: 100px;
  display: flex;

  &__bush {
    @extend %bush;
    width: 100%;
    height: 150px;
    bottom: -30px;
    position: relative;
    &::after {
      content: "";
      @extend %bush;
      position: absolute;
      width: 100px;
      height: 100px;
      left: 20px;
      bottom: 0;
    }
    &::before {
      content: "";
      @extend %bush;
      position: absolute;
      width: 30px;
      height: 120px;
      left: 40px;
      bottom: 0;
    }
  }
  &__bushik {
    @extend %bush;
    position: relative;
    width: 100%;
    height: 150px;
    bottom: -40px;
    &::after {
      content: "";
      @extend %bush;
      position: absolute;
      width: 200px;
      height: 100px;
      left: 20px;
      bottom: 0;
    }
    &::before {
      content: "";
      @extend %bush;
      position: absolute;
      width: 50px;
      height: 150px;
      bottom: 0;
      left: 70px;
    }
  }
}
