* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

//rocket

@font-face {
  font-family: "icomoon";
  src: url("fonts/icomoon.eot?trc2m0");
  src: url("fonts/icomoon.eot?trc2m0#iefix") format("embedded-opentype"),
    url("fonts/icomoon.ttf?trc2m0") format("truetype"),
    url("fonts/icomoon.woff?trc2m0") format("woff"),
    url("fonts/icomoon.svg?trc2m0#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-rocket:before {
  content: "\e9a5";
}

//colors

$bg_color: #71c5cf;

$road_color: #ddd894;

$column_color: #74bf2e;

$column_border: 5px solid #533944;

.container {
  width: 1200px;
  height: 800px;
  margin: 100px auto 0 auto;
  background-color: $bg_color;
  border: 15px solid #533944;
  overflow: hidden;
  position: relative;
}

body {
  background-color: #ebfcda;
}

//bushes

%bush {
  display: block;
  border: 3px solid #7ecc8e;
  border-radius: 50%;
  background-color: #85e38d;
  margin-right: 40px;
}

//columns

%columns {
  height: 1500px;
  width: 140px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

%column {
  width: 120px;
  height: 600px;
  background-color: #74bf2e;
  border: $column_border;
  position: relative;
  display: flex;
  justify-content: center;
  &::before {
    content: "";
    display: block;
    height: 100%;
    width: 10px;
    background-color: #567e25;
    position: absolute;
    right: 0;
  }
  &::after {
    content: "";
    display: block;
    height: 100%;
    width: 5px;
    background-color: #567e25;
    position: absolute;
    right: 15px;
  }
}

%column_head {
  height: 50px;
  width: 130px;
  background-color: $column_color;
  position: absolute;
  border: $column_border;
  &::before {
    content: "";
    display: block;
    height: 100%;
    width: 10px;
    background-color: #567e25;
    position: absolute;
    right: 0;
  }
  &::after {
    content: "";
    display: block;
    height: 100%;
    width: 5px;
    background-color: #567e25;
    position: absolute;
    right: 15px;
  }
}

%light__shadow {
  height: 100%;
  width: 40px;
  background-color: #9de85a;
  position: absolute;
  left: 0;
  &::before {
    content: "";
    display: block;
    height: 100%;
    width: 5px;
    background-color: #daf982;
    position: absolute;
    left: 5px;
  }
  &::after {
    content: "";
    display: block;
    height: 100%;
    width: 5px;
    background-color: $column_color;
    position: absolute;
    left: 30px;
  }
}
