.road {
  height: 70px;
  width: 6400px;
  background-color: $road_color;
  position: absolute;
  bottom: 0;
  border-top: 6px solid #d4a64e;
  z-index: 10;
  animation: road 20s linear forwards;
  &__border {
    height: 25px;
    width: 100%;
    background-color: #9de657;
    position: absolute;
    top: -31px;
    border-bottom: 6px solid #567f23;
    border-top: 6px solid #553d4b;
    overflow: hidden;
    &::after {
      content: "";
      display: block;
      width: 100%;
      border-top: 10px dashed #74bf2e;
      position: absolute;
      top: -2px;
    }
    &::before {
      content: "";
      display: block;
      width: 100%;
      border-top: 10px dashed #74bf2e;
      position: absolute;
      top: 6px;
      left: -5px;
    }
  }
}
